<style lang="less">
    @import './login.less';
</style>
<template>
    <div class="login">
        <div >
            <div class="main-header">
                <div class="main-logo-title">
                    {{$t('project_name')}}
                </div>
                <div style="position:absolute; top:20px; right:20px; font-size:14px;">
                    <Dropdown trigger="click" @on-click="handleSelectLanguage">
                        <a href="javascript:void(0)" >
                            Language
                            <Icon type="arrow-down-b"></Icon>
                        </a>
                        <DropdownMenu slot="list">
                            <DropdownItem name="zh-CN">中文简本</DropdownItem>
                            <DropdownItem name="en-US">English</DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </div>
            </div>
        </div>
        <div style="background-color: #fff;clear:both;">
            <div class="single-page-con" >
                <div class="single-page">
                    <div class="login-form">
                        <Row class="title">
                            {{$t('page_login.login_form.title')}}
                        </Row>
                        <Row class="form-con">
                            <Col span="6">
                                <Input v-model="login_form.code_a" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_b" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_c" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_d" class="input"></Input>
                            </Col>
                        </Row>
                        <Row class="form-con">
                            <Col span="6">
                                <Input  v-model="login_form.code_e" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_f" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_g" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_h" class="input"></Input>
                            </Col>
                        </Row>
                        <Row class="form-con">
                            <Col span="6">
                                <Input v-model="login_form.code_i" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_j" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_k" class="input"></Input>
                            </Col>
                            <Col span="6">
                                <Input v-model="login_form.code_l" class="input"></Input>
                            </Col>
                        </Row>
                        <Row class="link-gencode">
                            {{$t('page_login.login_form.desc')}}<a @click="genCodeModal_open">{{$t('page_login.login_form.a.gen_code')}}</a>
                        </Row>
                        <Row class="link-terms">
                            <a @click="termsModal_open">{{$t('page_login.login_form.a.show_privacy')}}</a>
                        </Row>
                        <Row class="btn-login">
                            <Button :disabled="disabledLoginBtn" type="primary" @click="login(1)" >{{$t('page_login.login_form.btn.login')}}</Button>
                        </Row>
                    </div>
                </div>
            </div>
        </div>
        <Modal v-model="showGetCodeModal" class-name="vertical-center-modal" width="550" @on-cancel="genCodeModal_close">
            <p slot="header">
                {{$t('page_login.gen_code_form.title')}}
            </p>
            <div class="gencode-form">
                <Row class="form-con">
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_a" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_b" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_c" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_d" class="input"></Input>
                    </Col>
                </Row>
                <Row class="form-con">
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_e" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_f" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_g" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_h" class="input"></Input>
                    </Col>
                </Row>
                <Row class="form-con">
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_i" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_j" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_k" class="input"></Input>
                    </Col>
                    <Col span="6">
                        <Input disabled v-model="gen_form.code_l" class="input"></Input>
                    </Col>
                </Row>
                <Row class="change">
                    <i class="ivu-icon ivu-icon-loop"></i>&nbsp;<a @click="genCode">{{$t('page_login.gen_code_form.a.change')}}</a>
                </Row>
            </div>
            <div slot="footer" style="text-align:center;">
                <Button :disabled="genLoginBtn.disabled"  type="primary" style="width:180px;" @click="login(2)">{{genLoginBtn.text}}</Button>
            </div>
        </Modal>
        <Modal v-model="showTermsModal" :closable="false" class-name="vertical-center-modal" width="650">
            <div slot="header"  style="text-align:center;">
                {{$t('page_login.privacy_modal.title')}}
            </div>
            <div>内容</div>
            <div slot="footer"  style="text-align:center;">
                <Button  type="primary" style="width:80px;" @click="termsModal_close" >{{$t('page_login.privacy_modal.btn_close')}}</Button>
            </div>
        </Modal>
        <Modal v-model="showSecret" :closable="false" :mask-closable="false" class-name="vertical-center-modal" width="350">
            <div slot="header"  style="text-align:center;">
                {{$t('page_login.secret_modal.title')}}
            </div>
            <div>
                <Input type="password" v-model="password" :placeholder="$t('page_login.secret_modal.password_placeholder')"></Input>
            </div>
            <div slot="footer"  style="text-align:center;">
                <Button  type="primary" style="width:80px;" @click="secretLogin">{{$t('page_login.secret_modal.btn_login')}}</Button>
            </div>
        </Modal>
    </div>
</template>
<script>
import Util from '../libs/util';
import wallet from '../libs/wallet/wallet';
import Vue from 'vue';
    export default {
        name: 'login',
        data() {
            return {
                disabledLoginBtn:false,
                showGetCodeModal:false,
                showTermsModal:false,
                disabledGenLogin:true,
                showSecret:false,
                secretLoading:false,
                password:'',
                genLoginBtn:{
                    duration:30,
                    disabled:true,
                    interval_count:0,
                    text:this.$t('page_login.gen_code_form.btn_login')+' （30s）'
                },
                login_form:{
                     code_a:'',
                     code_b:'',
                     code_c:'',
                     code_d:'',
                     code_e:'',
                     code_f:'',
                     code_g:'',
                     code_h:'',
                     code_i:'',
                     code_j:'',
                     code_k:'',
                     code_l:'',
                     
                },
                gen_form:{
                     code_a:'',
                     code_b:'',
                     code_c:'',
                     code_d:'',
                     code_e:'',
                     code_f:'',
                     code_g:'',
                     code_h:'',
                     code_i:'',
                     code_j:'',
                     code_k:'',
                     code_l:'',
                }
            };
        },
        methods: {
            init(){
                if(!Vue.config.productionTip)
                {
                     this.login_form.code_a='until';
                     this.login_form.code_b='spring';
                     this.login_form.code_c='liar';
                     this.login_form.code_d='detect';
                     this.login_form.code_e='track';
                     this.login_form.code_f='medal';
                     this.login_form.code_g='upper';
                     this.login_form.code_h='afraid';
                     this.login_form.code_i='owner';
                     this.login_form.code_j='innocent';
                     this.login_form.code_k='mixed';
                     this.login_form.code_l='obey';
                }
            },
            handleSelectLanguage (name) {
                localStorage.setItem('language',name);
                Vue.config.lang=name;
            },
            genCodeModal_open(){
                this.genCode();
            },
            genLoginBtnControl(){
                this.genLoginBtn.interval_count=this.genLoginBtn.duration;
                this.genLoginBtn.disabled=true;
                if ( this.timeout ) {
                    clearTimeout(this.timeout);
                }
                this.genLoginBtnTimeout();
            },
            genLoginBtnTimeout(){
                if(!this.showGetCodeModal)
                {
                    if ( this.timeout ) {
                        clearTimeout(this.timeout);
                    }
                    return false;
                }
                if (this.genLoginBtn.interval_count>0) {
                    // console.log('setInterval')
                    this.genLoginBtn.text=this.$t('page_login.gen_code_form.btn_login')+" （"+this.genLoginBtn.interval_count+"s）";
                    this.genLoginBtn.interval_count--;
                    let _this = this;
                    this.timeout = setTimeout(() => {
                        _this.genLoginBtnTimeout();
                    }, 1000);
                } else {
                    this.timeout = '';
                    this.genLoginBtn.text=this.$t('page_login.gen_code_form.btn_login');
                    this.genLoginBtn.disabled=false;
                    this.genLoginBtn.interval_count=0;
                }
            },
            genCodeModal_close(){
                wallet.removeMnemonic();
                for(let code in this.gen_form){
                    this.gen_form[code]='';
                }

                if ( this.timeout ) {
                    clearTimeout(this.timeout);
                }
            },
            termsModal_open()
            {
                this.showTermsModal=true;
            },
            termsModal_close()
            {
                this.showTermsModal=false;
            },
            genCode(){
                //取消timeout
                this.showGetCodeModal=false;

                //to do finished excute this.$Spin.hide();
                this.$Spin.show({
                    render: (h) => {
                        return h('div', [
                            h('Icon', {
                                'class': 'login-spin-icon-load',
                                props: {
                                    type: 'load-c',
                                    size: 22
                                }
                            }),
                            h('div', this.$t('page_login.spin.gen_code'))
                        ])
                    }
                });
                //保留这个timeout 1s,确保上一次timeout clear
                setTimeout(() => {
                    
                    let word=wallet.generateMnemonic();
                    let word_arr=word.split(' ');
                    let i=0;
                    for(let code in this.gen_form){
                        this.gen_form[code]=word_arr[i];
                        i++;
                    }
                    
                    this.showGetCodeModal=true;
                    this.genLoginBtnControl();
                    this.$Spin.hide();
                }, 1000);

                
                
            },
            login(login_type){
                
                this.$Spin.show({
                    render: (h) => {
                        return h('div', [
                            h('Icon', {
                                'class': 'login-spin-icon-load',
                                props: {
                                    type: 'load-c',
                                    size: 22
                                }
                            }),
                            h('div', this.$t('page_login.spin.logining'))
                        ])
                    }
                });                      
                //获取助记词，一键登录
                if(login_type==2)
                {
                    let word_arr=[];
                    for(let code in this.gen_form){
                        word_arr.push(this.gen_form[code]);
                    }
                    let word=word_arr.join(' ');
                    wallet.saveMnemonic(word,this.password);
                    // this.$Spin.hide(); 
                    this.$router.push({
                        name: 'index'
                    });
                }
                else if(login_type==1)
                {
                    if(wallet.hasSecret())
                    {
                        this.$Spin.hide(); 
                        this.showSecret=true;
                        return;
                    }
                    else{
                        let word_arr=[];
                        let check_flag=false;
                        for(let code in this.login_form){
                            if(this.login_form[code]=='')
                            {
                                check_flag=true;
                                break;
                            }
                            word_arr.push(this.login_form[code]);
                        }
                        
                        if(check_flag)
                        {
                            this.$Spin.hide();
                            this.$Modal.warning({
                                title: this.$t('page_login.modal.title'),
                                content: this.$t('page_login.modal.content_1'),
                                okText:this.$t('page_login.modal.btn_close')
                            });

                        }
                        else
                        {
                            let word=word_arr.join(' '); 
                            if(!wallet.validateMnemonic(word)){
                                this.$Spin.hide();
                                this.$Modal.warning({
                                    title: this.$t('page_login.modal.title'),
                                    content: this.$t('page_login.modal.content_2'),
                                    okText:this.$t('page_login.modal.btn_close')
                                });
                            }
                            else{
                                    wallet.saveMnemonic(word,wallet.readPassword());
                                    // this.$Spin.hide();
                                    this.$router.push({
                                        name: 'index'
                                    });
                            }
                        }
                    }
                    
                }
                               
            },
            secretLogin()
            {
                
                if(this.password=='')
                {
                    this.$Modal.warning({
                        title: this.$t('page_login.modal.title'),
                        content: this.$t('page_login.modal.content_3'),
                        okText:this.$t('page_login.modal.btn_close')
                    });
                }
                else
                {
                    if(wallet.eqSecret(this.password))
                    {
                        this.$Spin.show({
                                render: (h) => {
                                    return h('div', [
                                        h('Icon', {
                                            'class': 'login-spin-icon-load',
                                            props: {
                                                type: 'load-c',
                                                size: 22
                                            }
                                        }),
                                        h('div', this.$t('page_login.spin.logining'))
                                    ])
                                }
                            });
                        setTimeout(() => {
                            this.$store.state.wallet=new wallet(wallet.readMnemonic(wallet.readPassword()),wallet.readPassword());
                            
                            this.$Spin.hide();
                            this.$router.push({
                                name: 'index'
                            });
                        }, 1000);
                        
                    }
                    else
                    {
                        this.$Modal.warning({
                            title: this.$t('page_login.modal.title'),
                            content: this.$t('page_login.modal.content_4'),
                            okText:this.$t('page_login.modal.btn_close')
                        });
                    }
                }
            }
        },
        mounted() {
            this.init();
            // init page
            //if logined
            // this.$router.push({
            //             name: 'index'
            //         });
        },
        computed: {
            timeout: {
                set (val) {
                    this.$store.state.timeout.genLoginBtn = val;
                },
                get() {
                    return this.$store.state.timeout.genLoginBtn;
                }
            },
        },
        created() {
            if ( this.timeout ) {            
                clearTimeout(this.timeout);
            }
            if(wallet.hasMnemonic())
            {
                if(wallet.hasSecret())
                {
                    this.showSecret=true;
                }
                else{
                    this.$router.push({
                        name: 'index'
                    });
                }
            }
        },
        activated() {
            if ( this.timeout ) {
                clearTimeout(this.timeout);
            }
        },
        destroyed() {
            clearTimeout(this.timeout);
        },
        watch: {
            'login_form': {
                handler: function (val, oldVal) {
                    let flag_disabled=false;
                    for(let code in this.login_form){
                        if(this.login_form[code]==='')
                        {
                            flag_disabled=true;
                        }
                    }
                    this.disabledLoginBtn=flag_disabled;
                },
                deep: true
            }
        },
    };
</script>


